@font-face {
  font-family: 'OsloSans';
  src: url("./fonts/OsloSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-stretch: normal; }

@font-face {
  font-family: 'OsloSans';
  src: url("./fonts/OsloSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-stretch: normal; }

@font-face {
  font-family: 'OsloSans';
  src: url("./fonts/OsloSans-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-stretch: normal; }

.main-container {
  display: flex;
  flex-wrap: wrap; }

.main-container__item {
  padding: 5px;
  margin-top: 10px; }

.main-container__cards {
  display: flex;
  padding: 2rem;
  flex-flow: row wrap;
  justify-content: flex-start;
  min-width: 300px;
  max-width: 1300px;
  flex: 1 1 900px; }

.main-container__map {
  display: flex;
  padding: 2rem;
  flex-flow: row wrap;
  justify-content: flex-start;
  min-width: 300px;
  max-width: 500px;
  flex: 0 1 400px; }

svg.graph__svg g.tick text {
  font-size: 12px;
  font-weight: 500;
  color: #5e5e66;
  font-family: monospace;
  text-rendering: geometricPrecision; }

.graph__container {
  position: relative;
  margin: 1em 1em 0 1em;
  overflow: hidden; }

.graph__dropdown {
  position: absolute;
  top: 9.75em;
  left: 0;
  padding: 1em; }
  .graph__dropdown__label {
    display: block; }
  .graph__dropdown__select {
    border: 1px solid black;
    position: relative;
    height: 3em;
    padding-left: 0.5em;
    width: 7em;
    border-radius: 1px;
    margin-top: 0.5em; }
    .graph__dropdown__select::before, .graph__dropdown__select::after {
      position: absolute;
      display: block;
      content: '';
      height: 15px;
      width: 15px;
      background: black;
      top: 15px;
      left: 15px; }

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s; }

.fade-enter, .fade-leave-to {
  opacity: 0; }

html {
  background-color: #f7f7f7;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  overflow-y: scroll; }

body {
  margin: 0;
  min-height: 100vh; }

#app {
  font-family: 'OsloSans', sans-serif; }

.theme--light.v-select .v-select__selections {
  color: #8d8da0; }

h2.section-heading {
  color: #292858;
  width: 100%;
  padding: 1.5em 0.5em 0.5em; }

button,
input,
select {
  border-style: none;
  font: inherit;
  background-color: transparent; }

select {
  -webkit-appearance: none; }

* {
  margin: 0; }

*,
::before,
::after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

[role="button"] {
  cursor: pointer; }
